<template>
    <van-submit-bar
        :price="totalPrice"
        :button-text="submitBtn"
        :button-color="checkedColor"
        @submit="onSubmit"
        text-align="left"
        :class="{ opacity: checkedCount === 0 }"
    >
        <div @click="checkedAllClick">
            <van-checkbox
                v-model="checkedAll"
                :checked-color="checkedColor"
                ></van-checkbox
            >
        </div>
        <!-- <template #tip>
            ,
            <span @click="onClickEditAddress"></span>
        </template> -->
    </van-submit-bar>
</template>

<script>
import { SubmitBar, Checkbox } from 'vant';
import { mapGetters } from 'vuex';

export default {
    name: 'CartBottomBar',
    props: {
        checkedColor: String,
        // checkedAllClickvan-checkboxprops
        checkedAll: Boolean
    },
    components: {
        [SubmitBar.name]: SubmitBar,
        [Checkbox.name]: Checkbox
    },
    methods: {
        checkedAllClick() {
            //checked
            this.cartList.forEach(item => (item.checked = !this.checkedAll));
        },
        onSubmit() {
            if (this.checkedCount === 0) {
                this.$toast({
                    type: 'fail',
                    message: '',
                    forbidClick: true,
                    duration: 1500
                });
            }else {
                this.$toast({
                    type: 'fail',
                    message: '',
                    forbidClick: true,
                    duration: 1500
                });
            }
        }
    },
    computed: {
        ...mapGetters(['cartList']),
        // 
        checkedCount() {
            return this.cartList.filter(item => item.checked).length;
        },
        // 
        totalPrice() {
            return (
                this.cartList
                    .filter(item => item.checked)
                    .reduce((pre, item) => pre + item.price * item.count, 0) *
                100
            );
        },

        // 
        submitBtn() {
            return `(${this.checkedCount})`;
        }
    }
};
</script>

<style scoped>
.van-submit-bar {
    bottom: 49px;
    border-top: 1px solid rgb(204, 203, 203);
}
.van-submit-bar__text {
    display: flex;
    align-items: center;
    padding-left: 12px;
}
.van-button--normal {
    padding: 0;
    font-size: 16px;
}
.opacity .van-button {
    opacity: 0.5;
}
</style>