import {
    ADD_CART_COUNTER,
    ADD_CART,
    ADD_COLLECT,
    REMOVE_COLLECT
} from './types'

export default {
    // 
    setCartGoods({
        state,
        commit
    }, data) {
        return new Promise((resolve) => {
            // created
            // id
            let result = state.cartList.find(item => item.id === data.id);

            // actionscommit
            if (result) {
                //
                commit(ADD_CART_COUNTER, result);
                resolve({
                    type: 'text',
                    position: 'bottom',
                    message: `${result.count}`,
                });
            } else {
                //
                commit(ADD_CART, data);
                resolve({
                    type: 'success',
                    position: 'middle',
                    message: ``,
                })
            }
        })

    },
    // 
    setCollectGoods({
        state,
        commit
    }, data) {
        return new Promise((resolve) => {
            let result = state.collectList.find(item => item.id === data.id)

            if (result) {
                commit(REMOVE_COLLECT, result.id);
                resolve({
                    //
                    icon: 'cross',
                    message: ``,
                });
            } else {
                commit(ADD_COLLECT, data);
                resolve({
                    //
                    icon: '',
                    message: ``,
                });
            }
        })
    }
}
