<template>
    <div class="better-scroll" ref="bsScroll">
        <div class="better-content">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll';

export default {
    name: 'Bscroll',
    props: {
        /**
         * 1 scroll
         * 2 scroll
         * 3 scrollswipescroll
         */
        probeType: {
            type: Number,
            default: 1
        },
        // 
        bounce: {
            type: Boolean,
            default: true
        },
        // 
        mouseWheel: {
            type: Boolean,
            default: false
        },
        // click
        click: {
            type: Boolean,
            default: true
        },
        // 
        listenScroll: {
            type: Boolean,
            default: false
        },
        // 
        pullUp: {
            type: Boolean,
            default: false
        },
        // 
        pulldown: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            scroll: null
        };
    },
    // 
    mounted() {
        this.$nextTick(() => {
            this._initScroll();
        });
    },
    methods: {
        _initScroll() {
            // 
            this.scroll = new BScroll(this.$refs.bsScroll, {
                probeType: this.probeType,
                mouseWheel: this.mouseWheel,
                bounce: this.bounce,
                click: this.click
            });
            // 
            if (this.listenScroll) {
                this.scroll.on('scroll', pos => {
                    this.$emit('inScroll', pos);
                });
            }
            // 
            if (this.pullUp) {
                this.scroll.on('scrollEnd', pos => {
                    if (pos.y <= this.scroll.maxScrollY + 50) {
                        this.$emit('scrollEnd');
                    }
                });
            }
            // 
            if (this.pulldown) {
                this.scroll.on('touchend', pos => {
                    // 
                    if (pos.y > 50) {
                        this.$emit('pulldown');
                    }
                });
            }
        },
        refresh() {
            // better-scrollrefresh
            this.scroll && this.scroll.refresh();
        },
        scrollTo(x, y, time = 300) {
            // better-scrollscrollTo
            this.scroll && this.scroll.scrollTo(x, y, time);
        },
        scrollToElement(el, time = 300) {
            // better-scrollscrollToElement
            this.scroll && this.scroll.scrollToElement(el, time);
        },
        getScrollY() {
            return this.scroll ? this.scroll.y : 0;
        }
    }
};
</script>

<style scoped>
.better-scroll {
    overflow: hidden;
}
.better-content {
    position: relative;
}
.pull-msg {
    position: absolute;
    width: 100%;
    font-size: 12px;
    text-align: center;
}
.pull-msg-up {
    top: -20px;
}
.pull-msg-down {
    bottom: -20px;
}
</style>