import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getter'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

const state = {
    // tabbar
    tabBarShow: true,
    // 
    cartList: [],
    // 
    collectList: [],
    // 
    isLoading: true
}

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions
})
