<template>
    <div id="home">
        <nav-bar class="home-nav">
            <template v-slot:nav-center></template>
        </nav-bar>
        <tab-control
            v-show="isTabShow"
            :titles="titles"
            @tabClick="tabClick"
            class="home-tab-control"
            ref="tabControlFixed"
        />
        <bscroll
            class="home-scroll"
            ref="scroll"
            :probe-type="3"
            :mouse-wheel="true"
            :pull-up="true"
            :listen-scroll="true"
            @inScroll="contScroll"
            @scrollEnd="contScrollEnd"
        >
            <bs-swiper :banners="homeBanners" />
            <recommend-view :recommends="recommend" />
            <feature-view />
            <tab-control
                class="home-tab-control"
                :titles="titles"
                @tabClick="tabClick"
                ref="tabControl"
            />
            <goods-list :goods-list="goods[curType].list" />
        </bscroll>
        <back-top @click.native="backClick" v-show="isBackTop" />
    </div>
</template>

<script>
// 
import NavBar from '_com/common/navbar/NavBar';
import Bscroll from '_com/common/bscroll/Bscroll';
import BsSwiper from '_com/common/bsswiper/BsSwiper';
import TabControl from '_com/content/tab-control/TabControl';
import GoodsList from '_com/content/goods/GoodsList';

// 
import RecommendView from './children/RecommendView';
import FeatureView from './children/FeatureView';

// 
import { getHomeMultidata, getHomeGoods } from '_new/home';

// 
import {
    goodsListenerMixin,
    swiperListenerMixin,
    backTopMixin
} from '_con/mixin';

export default {
    name: 'Home',
    components: {
        NavBar,
        Bscroll,
        BsSwiper,
        TabControl,
        GoodsList,
        RecommendView,
        FeatureView
    },
    data() {
        return {
            // 
            homeBanners: [],
            recommend: [],
            keywords: [],
            titles: ['', '', ''],
            goods: {
                pop: { page: 0, list: [] },
                new: { page: 0, list: [] },
                sell: { page: 0, list: [] }
            },
            curType: 'pop',
            tabOffsetTop: 0
        };
    },
    mixins: [goodsListenerMixin, swiperListenerMixin, backTopMixin],
    created() {
        // 1.
        this.getHomeMultidata();
        // 2.
        this.getHomeGoods('pop');
        this.getHomeGoods('new');
        this.getHomeGoods('sell');
    },
    beforeMount() {
        // swipertabControl
        this.swiperImgListener = () => {
            this.tabOffsetTop = this.$refs.tabControl.$el.offsetTop;
        };
    },

    activated() {
        this.$refs.scroll.refresh();
    },
    deactivated() {
        // swiper
        this.$bus.$off('swiperImgLoad', this.swiperImgListener);

        // itemImage
        this.$bus.$off('itemImageLoad', this.goodstImgListener);
    },
    computed: {
        isTabShow() {
            // 
            return this.curPosition > this.tabOffsetTop;
        }
    },
    methods: {
        // 
        contScroll(position) {
            // 
            this.curPosition = position ? -position.y : 0;
        },

        // tabCtrol
        tabClick(index) {
            switch (index) {
                case 0:
                    this.curType = 'pop';
                    break;
                case 1:
                    this.curType = 'new';
                    break;
                case 2:
                    this.curType = 'sell';
                    break;
            }
            this.$refs.tabControlFixed.curIndex = index;
            this.$refs.tabControl.curIndex = index;
            // 
            this.$refs.scroll.scrollTo(0, -this.tabOffsetTop, 50)
        },

        // 
        contScrollEnd() {
            this.getHomeGoods(this.curType);
        },

        // 
        getHomeMultidata() {
            getHomeMultidata().then(res => {
                this.homeBanners = res.data.banner.list;
                this.recommend = res.data.recommend.list;
                this.keywords = res.data.keywords.list;
            });
        },

        // 
        getHomeGoods(type) {
            const page = this.goods[type].page + 1;
            getHomeGoods(type, page).then(res => {
                this.goods[type].list.push(...res.data.list);
                this.goods[type].page += 1;
            });
        }
        // 
        // setSessionStorage() {
        //     // sessionStorage
        //     if (sessionStorage.getItem('homePosition')) {
        //         this.setTop = parseFloat(
        //             sessionStorage.getItem('homePosition')
        //         );
        //     }
        //     // sessionStorage
        //     window.addEventListener('beforeunload', () => {
        //         sessionStorage.setItem('homePosition', -this.curPosition);
        //     });
        // }
    }
};
</script>

<style scoped>
#home {
    position: relative;
    height: 100vh;
}
.home-nav {
    position: relative;
    z-index: 9;
    width: 100%;
    background-color: var(--color-tint);
    color: #fff;
}
.home-tab-control {
    position: relative;
    z-index: 99;
    background-color: #fff;
}
.home-scroll {
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 49px;
}
</style>